import React from 'react'
import "./Subjects.css";
import Navbar from '../Navbar/Navbar'
import { NavLink,Link } from "react-router-dom";
function Subjects() {
    return (
      <>
        <Navbar head="Home" />
        <div className="container">
          <Link to="/spm" style={{ cursor: "pointer", textDecoration: "none" }}>
            <div className="card1 my-3">
              <h1>SPM</h1>
              <h2></h2>
            </div>
          </Link>
          <Link
            to="/aplr"
            style={{ cursor: "pointer", textDecoration: "none" }}
          >
            <div className="card2 my-3">
              <h1>APLR</h1>
            </div>
          </Link>
          <Link
            to="/ittool"
            style={{ cursor: "pointer", textDecoration: "none" }}
          >
            <div className="card3 my-3">
              <h1>IT TOOL {`&`} WEB III</h1>
            </div>
          </Link>
          <Link
            to="/aand"
            style={{ cursor: "pointer", textDecoration: "none" }}
          >
            <div className="card4 my-3">
              <h1>Adv. Android</h1>
            </div>
          </Link>
          <Link
            exact
            to={{
              pathname: "/adbms",
              hash: "#",
              state: { fromDashboard: true },
            }}
            style={{ cursor: "pointer", textDecoration: "none" }}
          >
            <div className="card5 my-3">
              <h1>ADBMS</h1>
            </div>
          </Link>
          <Link
            to="/corejava"
            style={{ cursor: "pointer", textDecoration: "none" }}
          >
              <div className="card6 my-3">
                <h1>CORE JAVA</h1>
              </div>
          </Link>
        </div>
      </>
    );
}

export default Subjects
